import { getQuery } from 'api/azureQuery';
import React from 'react';
import { translate } from 'react-i18next';
import { connect } from 'react-redux';
import { azureReportsActions, azureReportsSelectors } from 'store/azureReports';
import { createMapStateToProps } from 'store/common';
const reportType = "tag" /* tag */;
class DetailsTagViewBase extends React.Component {
    constructor() {
        super(...arguments);
        this.getTags = () => {
            const { report } = this.props;
            const tags = [];
            if (report) {
                for (const tag of report.data) {
                    if (Array.isArray(tag.values)) {
                        for (const val of tag.values) {
                            tags.push(`${tag.key}: ${val}`);
                        }
                    }
                    else {
                        tags.push(`${tag.key}: ${tag.values}`);
                    }
                }
            }
            return tags;
        };
    }
    componentDidMount() {
        const { fetchReport, queryString } = this.props;
        fetchReport(reportType, queryString);
    }
    componentDidUpdate(prevProps) {
        const { fetchReport, queryString } = this.props;
        if (prevProps.queryString !== queryString) {
            fetchReport(reportType, queryString);
        }
    }
    render() {
        const tags = this.getTags();
        return tags.map((tag, index) => React.createElement("div", { key: `tag-${index}` }, tag));
    }
}
const mapStateToProps = createMapStateToProps((state, { account }) => {
    const queryString = getQuery({
        filter: {
            subscription_guid: account,
            resolution: 'monthly',
            time_scope_units: 'month',
            time_scope_value: -1,
        },
    });
    const report = azureReportsSelectors.selectReport(state, reportType, queryString);
    const reportFetchStatus = azureReportsSelectors.selectReportFetchStatus(state, reportType, queryString);
    return {
        queryString,
        report,
        reportFetchStatus,
    };
});
const mapDispatchToProps = {
    fetchReport: azureReportsActions.fetchReport,
};
const DetailsTagView = translate()(connect(mapStateToProps, mapDispatchToProps)(DetailsTagViewBase));
export { DetailsTagView };
